<template>
    <el-card class="box-card" :body-style="{'background-color': '#ffffff'}">
        <div slot="header" class="clearfix">
            <span>用户信息</span>
        </div>
        <table class="usermessage">
            <tr>
                <td>用户名：</td>
                <td>{{this.Account}}</td>
            </tr>
            <tr>
                <td>手机：</td>
                <td>{{'+'+this.PhonePrefix+'&nbsp'+this.PhoneNumber}}</td>
            </tr>
            <tr>
                <td>邮箱：</td>
                <td>{{this.Email}}</td>
            </tr>
        </table>
    </el-card>
</template>
<script>
    export default{
        name: 'offlineUserInfo',
        data(){
            return{
                Account:'',
                PhoneNumber:'',
                PhonePrefix:'',
                Email:''
            }
        },
        props:['accountId', 'title'],
        methods: {
            getOfflineUserInfo(){
                this.$api('/admin/offline/get_offline_user_info', {
                    accountId:this.accountId
                }).then(res => {
                    // console.log(res)
                    let d = res.data
                    this.Account = d.Account
                this.PhoneNumber = d.PhoneNumber
                this.PhonePrefix = d.PhonePrefix
                this.Email = d.Email
            });
            }
        },
        created(){
            // console.log(this.accountId)
            this.getOfflineUserInfo()
        }
    }
</script>
<style lang='scss' scoped>
    @import "../assets/css/titlecom.scss";
    .usermessage{
        width: 100%
    }
    .usermessage td{
        border: 1px solid #ebeef5
    }
    .usermessage tr td:first-child{
        width: 15%;
        height: 40px;
        font-size: 14px;
        font-weight: 900;
        padding-left: 30px
    }
    .usermessage tr td:nth-child(2){
        width: 85%;
        font-size: 14px;
        padding-left: 30px;
    }
</style>


